//*! ======== MOBILE ========*/

@media screen and (min-width: 375px) {
    .empty-section {
        display: flex;
        justify-content: center; 
        align-items: center;
        height: 100%; 
        padding: 40px 20px;
        
    }
    
    .empty-container {
        display: flex;
        flex-direction: column;
        position: relative; 
        width: 100%;
    }
    
    .shopping-title {
        color: #111;
        font-size: 32px;
        font-weight: 700;
        line-height: 1.18;
        letter-spacing: -1.28px;
    
        width: 193px;
        height: 38px;
    
        position: absolute; 
        top: 0;
        left: 0;
    }
    .colored-text {
        color: #4F2EE8;
    }
    
    .wrap-empty-desc {
        margin-left: auto;
        margin-right: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 14px;
    
        text-align: center;
        padding-top: 158px;
    }
    
    .empty-desc {
        color: rgba(0, 0, 0, 0.60);
        text-align: center;
        font-family: DM Sans;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 1.28;  
        letter-spacing: -0.28px;
    
        max-width: 244px;;
    }
    .empty-img {
        width: 265px;
        height: 198px;
    }
}


//*! ======== TABLET ========*/

@media screen and (min-width: 768px) {
    .empty-section {
        padding: 40px 32px;
        
    }
    .shopping-title {
        font-size: 48px;
        line-height: 1.08; 
        letter-spacing: -1.92px;
    
        width: 292px;
        height: 52px;
    }
    .wrap-empty-desc {
        
        gap: 20px;
        padding-top: 192px;
    }
    .empty-desc {
        font-size: 18px;
        line-height: 1.33;  
        letter-spacing: -0.36px;
    
        max-width: 349px;;
    }
    .empty-img {
        width: 322px;
        height: 241px;
    }
}

//*! ======== DESKTOP ========*/
@media screen and (min-width: 1440px) {
    .empty-section {
        padding: 40px 40px;
    }
  }


