.body{
    overflow-x: hidden;
}
.popup-img {
    width: 287px;
    height: 408px;
    margin-top: 28px;
    border-radius: 8px;
    display: block;
    margin-left: auto;
    margin-right: auto;
}

.content{
    margin-top: 14px;
}

.modal {
    width: 335px;
    background-color: #fff;
    box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.14), 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 2px 1px rgba(0, 0, 0, 0.2);
    border-radius: 18px;
    border: 2px solid #111;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(1);
    transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
    padding: 12px 12px 40px;
    overflow: auto;
}

.modal-close {
    //margin-top: 4px;
    width: 24px;
    height: 24px;
    display: block;
    position: absolute;
    right: 12px;
    //top: 24px;
    border: none;
    background-color: transparent;
    outline: none;
    background: url('../images/close.png');
}

.title {
    color: #111;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    /* 112.5% */
    letter-spacing: -0.64px;
    margin-top: 14px;
    padding: 0 12px;
}

.author {
    margin-top: 4px;
    padding: 0 12px;
    color: #B4AFAF;
    font-size: 12px;
    font-style: italic;
    font-weight: 400;
    line-height: 14px;
    /* 116.667% */
    letter-spacing: -0.48px;
}

.text {
    margin-top: 18px;
    padding: 0 12px;
    color: #111;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 18px;
    /* 128.571% */
    letter-spacing: -0.56px;
}

.shop-list {
    margin-top: 14px;
    padding: 0 12px;
    display: flex;
    gap: 20px;
    align-items: center;
}

.shop-img {
    filter: grayscale(100%);
}

.add-to-cart-btn {
    margin-top: 40px;
    border-radius: 40px;
    border: 2px solid #4F2EE8;
    display: block;
    padding: 14px 28px;
    margin-right: auto;
    margin-left: auto;
    background-color: #fff;
    color: #111;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 18px;
    /* 128.571% */
    letter-spacing: -0.14px;

}

.text-under-btn{
    color: rgba(17, 17, 17, 0.50);
    text-align: center;
    font-size: 12px;
    font-style: normal;
    font-weight: 400;
    line-height: 14px;
    /* 116.667% */
    letter-spacing: -0.48px;
    margin: 8px auto 32px;
    max-width: 280px;
}

.disable-scroll{
    overflow: hidden;
}



@media screen and (min-width: 768px) and (max-width: 1439px) {

    .popup-img {
        width: 192px;
        height: 281px;
        margin-left: 0;
        margin-top: 0;
        margin-right: 0;

    }

    .modal {
        width: 579px;
        padding: 40px;
        display: flex;
        //gap: 24px;
        flex-wrap: wrap;
    }

    .modal-close {
        position: absolute;
        top: 24px;
        right: 24px;
        border: none;
        background-color: transparent;
        outline: none;
        background: url('../images/close.png');
    }

    .title {
        padding: 0;
        margin-top: 0;
        font-size: 24px;
        line-height: 28px;
        letter-spacing: -0.96px;
    }

    .author{
        margin-top: 8px;
        padding: 0;
        font-size: 14px;
        line-height: 18px;
        /* 128.571% */
        letter-spacing: -0.56px;
    }

    .text {
        margin-top: 20px;
        max-width: 279px;
        padding: 0;
    }

    .shop-list{
        margin-top: 22px;
        padding: 0;
        align-items: center;
    }

    .shop-img {
        filter: grayscale(100%);
    }

    .shop-link:hover {
        filter: drop-shadow(0px 3px 3px rgba(0, 0, 0, .35));
    }

    .text-under-btn{
        max-width: 324px;
    }

    .add-to-cart-btn {
        margin-top: 40px;
        padding: 20px 100px;
        width: 100%;
    }

    .add-to-cart-btn:hover {
        box-shadow: 5px 3px 4px 2px #4F2EE8;
    }

    .content {
        margin-top: 0;
        max-width: 279px;
        margin-left: 24px;
    }
}

